ï»¿#harbourInfo {
    -moz-box-shadow: 0 0 10px 5px #000000;
    -moz-box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.4);
    -ms-box-shadow: 0 0 10px 5px #000000;
    -webkit-box-shadow: 0 0 10px 5px #000000;
    -webkit-box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.4);
    background-color: white;
    border-radius: 5px;
    bottom: 30px;
    box-shadow: 0 0 10px 5px #000000;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.4);
    min-width: 400px;
    position: absolute;
    right: 30px;
    top: 30px;
    width: 30%;
    padding: 10px;


    .star {
        -moz-transform: rotate(35deg);
        -ms-transform: rotate(35deg);
        -o-transform: rotate(35deg);
        -webkit-transform: rotate(35deg);
        border-bottom: 14px solid #808080;
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        color: #808080;
        display: inline-block;
        height: 0px;
        margin: 0;
        position: relative;
        width: 0px;


        &:before {
            -moz-transform: rotate(-35deg);
            -ms-transform: rotate(-35deg);
            -o-transform: rotate(-35deg);
            -webkit-transform: rotate(-35deg);
            border-bottom: 16px solid #808080;
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            content: '';
            display: inline-block;
            height: 0;
            left: -13px;
            position: absolute;
            top: -11px;
            width: 0;
        }

        &:after {
            -moz-transform: rotate(-70deg);
            -ms-transform: rotate(-70deg);
            -o-transform: rotate(-70deg);
            -webkit-transform: rotate(-70deg);
            border-bottom: 14px solid #808080;
            border-left: 20px solid transparent;
            border-right: 20px solid transparent;
            color: #808080;
            content: '';
            display: inline-block;
            height: 0px;
            left: -21px;
            position: absolute;
            top: 0.6px;
            width: 0px;
        }

        &.active, &:hover {
            border-bottom-color: #ffd700;
            color: #ffd700;


            .star.active:before, .star:hover:before {
                border-bottom-color: #ffd700;
            }

            .star.active:after, .star:hover:after {
                border-bottom-color: #ffd700;
                color: #ffd700;
            }
        }
    }
}
